.stackStyle {
 position: sticky;
    background: #000;
    top: 0;
    justify-content: space-between;
}

.link {
    display: flex;
    align-items: center;
}

.imgLogo {
    height: 45px;
}


/* SearchBar */

.paper {
    border-radius: 20px;
    border: 1px solid #e3e3e3;
    padding-left: 2px;
    box-shadow: none;
    margin: auto;
}
.iconButton {
    padding: 10px;
    color: red;
}

.redBoxLogo {
    height: 40px;
    width: 60px;
    border-radius: 10px;
    background-color: red;
    align-items: center;
}

.whiteBoxLogo {
    margin: 13px auto;
    height: 14px;
    width: 14px;
}

.whiteLogo {
    border: 7px solid transparent;
    border-left: 20px solid white;
}
